import { Injectable } from '@angular/core';
import { CourseCatalogFakeDB } from '../../assets/fakeDB/coursesCatalog';
import { Course } from './course.model';

@Injectable({
  providedIn: 'root'
})
export class CoursesService {
  constructor() {
  }

  async getCourseCatalog(): Promise<Course[]>{
    const courseCatalog = await CourseCatalogFakeDB.coursesCatalog.slice();
     return courseCatalog;
  }
}

